<template>
    <div class="article">
        <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottomAllLoaded="bottomAllLoaded" ref="loadmore">
            <ul class="list-ul">
                <li class="list-li" v-for="item in list">
                    <div  class="item-content">
                        <div class="item-media">
                            <img class="item-img" src="http://1.jpg">
                        </div>
                        <div class="item-inner">
                            <div class="item-title-row">
                                <div class="item-title"> {{ item.title }}</div>
                            </div>
                            <div class="item-text">{{ item.created }}</div>
                        </div>
                    </div>
                </li>
            </ul>
        </mt-loadmore>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                list:[],
                bottomAllLoaded:false
            };
        },
        methods: {
            loadBottom(){
                this.list.push('觉得是返回士大夫似的看风使舵  撒范德萨');
                this.bottomAllLoaded = true;// if all data are loaded
                this.$refs.loadmore.onBottomLoaded();
            },
            loadTop() {
                let APP = this;
                APP.$Api.post({},r=>{
                    APP.$Func.setLocalArticle(r);
                    APP.list = r;
                    APP.$Func.toast('已经刷新当前文章','center');
                    APP.$refs.loadmore.onTopLoaded();
                });

            },
            loadArtcle(){
                let APP = this;
                APP.$Api.post({},r=>{
                    APP.$Func.setLocalArticle(r);
                    APP.list = r;
                });
            }
        },
        mounted: function () {
            let APP = this;
            let article = APP.$Func.getLocalArticle();
            if(article) {
                APP.list = article;
            }else {
                APP.loadArtcle();
            }
            setTimeout(()=>{if(this.$Func.isCordova()){window.navigator.splashscreen.hide();}},50);
        }
    }
</script>
<style lang="less">
    .article {
        .mint-loadmore{
            min-height: calc(~'100vh - 94px');
        }
        background: #f8f9fa;
        .list-ul {
            list-style: none;
            padding: 0;
            position: relative;
        }
        .list-li {
            min-height: 4rem;
            margin: 0.8rem 0;
            background-color: #fff;
            box-sizing: border-box;
            position: relative;
            border-bottom: 1px solid #e8e9eb;
            border-top: 1px solid #e8e9eb;
        }
        .item-content {
            box-sizing: border-box;
            padding-left: .75rem;
            padding-right: .75rem;
            min-height: 2.2rem;
            display: -webkit-box;
            display: -webkit-flex;
            display: flex;
            align-items: center;
        }
        .item-media {
            padding-top: .45rem;
            padding-bottom: .5rem;
            display: -webkit-box;
            display: -webkit-flex;
            display: flex;
            -webkit-flex-shrink: 0;
            -ms-flex: 0 0 auto;
            -webkit-flex-shrink: 0;
            flex-shrink: 0;
            -webkit-box-lines: single;
            -moz-box-lines: single;
            -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
            box-sizing: border-box;
            -webkit-box-align: center;
            -webkit-align-items: center;
            align-items: center;
        }
        .item-inner {
            position: relative;
            padding-left: 0.5rem;
            display: block;
            padding-top: .5rem;
            padding-bottom: .45rem;
            -webkit-align-self: stretch;
            align-self: stretch;

        }
        .item-img {
            width: 6rem;
            height: 4rem;
        }
        .item-title {
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            color: #333;
            font-size: 14px;
        }
        .item-text {
            white-space: nowrap;
            bottom: 0;
            position: absolute;
            font-size: 12px;
            padding-bottom: .45rem;
            color: #afafaf;
        }
    }
</style>


